import React from "react";
import {
  AppBar,
  makeStyles,
  Toolbar,
  Card,
  CardMedia,
  Typography,
  CardContent,
  CardActionArea,
  CssBaseline,
  Grid,
} from "@material-ui/core";
import CakeIcon from "@material-ui/icons/Cake";
import PhoneIcon from "@material-ui/icons/Phone";
import MailIcon from "@material-ui/icons/Mail";
import HomeIcon from "@material-ui/icons/Home";
import { SocialIcon } from "react-social-icons";
import GaneshPic from "../img/ganesh.png";


export default function Home() {
  const useStyle = makeStyles((theme) => ({
    rootBg: {
      padding: theme.spacing(2),
      background: "linear-gradient(to left bottom, #CDF0EA, #E4EFE7)",
    },
    AppBar: {
      backgroundColor: "#F96D15",
    },
    AppBarName: {
      fontSize: "20px",
      fontWeight: "bold",
      padding: theme.spacing(2),
    },
    MenuIcon: {
      display: "block",
      marginLeft: "auto",
    },
    centerText: {
      textAlign: "center",
      padding: theme.spacing(0.4),
    },
    details: {
      justify: "center",
      alignItems: "center",
      padding: theme.spacing(2),
    },
    cardImageRoot: {
      [theme.breakpoints.down("md")]: {
        width: "80%",
        display: "block",
        marginLeft: "auto",
        marginRight: "auto",
        marginTop: theme.spacing(10),
      },
      [theme.breakpoints.up("md")]: {
        width: "25%",
        display: "block",
        marginLeft: "auto",
        marginRight: "auto",
        marginTop: theme.spacing(13),
      },
    },
    downloadResume: {
      textTransform: "none",
      backgroundColor: "#F96D15",
      borderRadius: "none",
      position:'fixed',
      bottom:'3%',
      right:'3%',
    },
    about:{
      fontWeight:'bold',
      fontSize:'24px',
      textAlign:'center',
      padding:theme.spacing(3),     
    },
    aboutContent:{
      fontSize:'20px',
      textAlign:'center',
    },
  }));

  const link = [
    "https://twitter.com/Ganesh_jdev",
    "https://www.youtube.com/c/GaneshJDev",
    "https://www.instagram.com/mb_ga.ne.sh_dev/",
    "https://www.linkedin.com/in/ganesh-jdev/",
    "https://github.com/mbganesh",
    "https://www.facebook.com/ganesh.jdev",
    "https://play.google.com/store/apps/dev?id=7003456865684981446",
    "https://wa.me/+918838531474",
  ];

  const classes = useStyle();

  return (
    <>
      <div className={classes.rootBg}>
        <CssBaseline />

        <AppBar className={classes.AppBar}>
          <Toolbar>
            <Typography className={classes.AppBarName}>JDEV</Typography>
            {/* <MenuIcon className={classes.MenuIcon} /> */}
          </Toolbar>
        </AppBar>

        <Card className={classes.cardImageRoot}>
          <CardActionArea>
            <CardMedia component="img" alt="Ganesh JDev" image={GaneshPic} />
          </CardActionArea>
          <CardContent>
            <Typography
              className={classes.centerText}
              variant="body2"
              color="textSecondary"
              component="p"
            >
              HELLO EVERYBODY, I AM
            </Typography>
            <Typography
              className={classes.centerText}
              gutterBottom
              variant="h5"
              component="h2"
            >
              BoothaGanesh!!!
            </Typography>
          </CardContent>
        </Card>
          
        <div>
          <Typography className={classes.about}>About Me</Typography>
          <Typography className={classes.aboutContent}>
            Hello, I'm a Ganesh, android-developer based on Tamilnadu. I have
            rich experience in android development , web site design &amp;
            building and customization. Also I am good at Java , React , Mongo DB , UI/UX
          </Typography>

         <hr style={{width:'50%' , color:'black' , marginTop:'10px' }} />        
     
        </div>

        <div className={classes.details}>
          <div style={{ display: "flex", justifyContent: "center" }}>
            <Typography>
              <CakeIcon style={{ verticalAlign: "sub" }} /> 17 - SEP - 2K
            </Typography>
          </div>
          <div style={{ display: "flex", justifyContent: "center" }}>
            <Typography>
              <a
                href="tel:+918489991253"
                style={{ color: "black", textDecorationLine: "none" }}
              >
                <PhoneIcon style={{ verticalAlign: "sub" }} /> +91 8489991253{" "}
              </a>
            </Typography>
          </div>
          <div style={{ display: "flex", justifyContent: "center" }}>
            <Typography>
              <MailIcon style={{ verticalAlign: "sub" }} />
              <a
                href="mailto:mbganesh.jdev@gmail.com"
                style={{ color: "black", textDecorationLine: "none" }}
              >
                mbganesh.jdev@gmail.com
              </a>
            </Typography>
          </div>
          <div style={{ display: "flex", justifyContent: "center" }}>
            <Typography>
              <HomeIcon style={{ verticalAlign: "sub" }} />
              Tirunelveli,Tamilnadu
            </Typography>
          </div>
        </div>
        <hr style={{width:'25%' , color:'black' , marginBottom:'10px' , marginTop:'10px' }} />
        <div>
          <Grid container className={classes.root} spacing={2}>
            <Grid item xs={12}>
              <Grid container justify="center" spacing={5}>
                {link.map((value) => (
                  <Grid key={value} item>
                    <SocialIcon url={value} />
                  </Grid>
                ))}
              </Grid>
            </Grid>
          </Grid>
        </div>
      </div>
    </>
  );
}
