import PlayStoreApp from "./Components/PlayStoreApp";
import React from "react";
import Education from "./Components/Education";
import Experience from "./Components/Experience";
import Home from "./Components/Home";
import { Helmet } from "react-helmet";
import Footer from "./Components/Footer";
import { Button, makeStyles, Snackbar } from "@material-ui/core";
import Resume from "./pdf/BoothaGanesh_Resume.pdf";
import DownloadIcon from "@material-ui/icons/GetApp";
import Serivce from "./Components/Serivce";
import { Alert } from "@material-ui/lab";

export default function App() {
  const [open, setOpen] = React.useState(false);

  const handleClick = () => {
    setOpen(true);
  };

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };
  const useStyle = makeStyles((theme) => ({
   
    
    downloadResume: {
      textTransform: "none",
      backgroundColor: "#F96D15",
      borderRadius: "none",
      position:'fixed',
      bottom:'3%',
      right:'3%',
    },

    
  }));

  const classes = useStyle();

  return (
    <div>
      <Helmet>
        <title> Ganesh Profile</title>
        <meta name="description" content="Ganesh Resume"></meta>
      </Helmet>
      <Home />
      <Serivce/>
      <Experience />
      <Education />
      <PlayStoreApp />
      <Footer/>

      <Button
        className={classes.downloadResume}
        buttonStyle={{ borderRadius: 50 }}
        style={{ borderRadius: 50 }}
        variant="contained"
        color="primary"
        onClick={handleClick}
      >
       
        <a 
          style={{textDecoration:'none' , color:'white'}}
          href={Resume}
          download="BoothaGanesh_Resume"
          target="_blank"
        >
           Download CV <DownloadIcon style={{ verticalAlign: "sub" }} />
        </a>
      </Button>
      <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="success" variant="filled">
          Resume Downloaded!
        </Alert>
      </Snackbar>
    </div>
  );
}

